import React, { useState, useRef, useEffect } from "react";
import styles from "../styles/audioVisualizer.module.css";

export default function AudioVisualizer(props) {
  //audio element setup;
  const audioRef = useRef(new Audio());

  //canvas ref setup
  const canvasRef = useRef(null);
  const requestIdRef = useRef(null);

  //keep track if audio should play or not
  const [playAudio, setPlayAudio] = useState(null);

  function handleClick() {
    setPlayAudio(!playAudio);
  }

  // handles audio play
  useEffect(() => {
    if (playAudio) {
      audioRef.current.play();
    } else if (!playAudio) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
    }
  }, [playAudio]);

  //update audio element when question changes
  useEffect(() => {
    audioRef.current.src = props.audio;
    audioRef.current.crossOrigin = "anonymous";
    audioRef.current.onended = function () {
      setPlayAudio(false);
    };
    setPlayAudio(false);
  }, [props]);

  //on mount create audiocontext and canvas setup
  useEffect(() => {
    //audio context setup
    const AudioContext = window.AudioContext || window.webkitAudioContext;
    const audioCtx = new AudioContext();
    const src = audioCtx.createMediaElementSource(audioRef.current);

    //analyser setup
    const analyser = audioCtx.createAnalyser();
    src.connect(analyser);
    analyser.connect(audioCtx.destination);
    analyser.fftSize = 2 ** 8;
    const bufferLength = analyser.frequencyBinCount;
    const dataArray = new Uint8Array(bufferLength);

    //canvas setup
    let canvas = canvasRef.current;
    let context = canvas.getContext("2d");
    let requestId = requestIdRef.current;

    //animation settings

    /////////   FREQUENCY GRAPH  /////////
    /*
    let barHeight;
    let barWidth = 3;

    const animate = () => {
      
      context.clearRect(0, 0, canvas.width, canvas.height);
      analyser.getByteFrequencyData(dataArray);
      let x = 0;
      for (let i = 0; i < bufferLength; i++) {
        barHeight = dataArray[i] / 2;
        context.fillStyle = "red";
        context.fillRect(x, canvas.height - barHeight, barWidth, barHeight);
        x += barWidth;
      }
      requestId = requestAnimationFrame(animate);
    };
    */

    //////// OSCILOSCOPE GRAPH /////////
    const animate = () => {
      context.clearRect(0, 0, canvas.width, canvas.height);

      analyser.getByteTimeDomainData(dataArray);

      context.lineWidth = 3;
      context.strokeStyle = "red";
      context.beginPath();

      const sliceWidth = canvas.width / bufferLength;
      let x = 0;

      for (let i = 0; i < bufferLength; i++) {
        const v = dataArray[i] / 128;
        const y = (v * canvas.height) / 2;

        if (i === 0) {
          context.moveTo(x, y);
        } else {
          context.lineTo(x, y);
        }

        x += sliceWidth;
      }
      //context.lineTo(canvas.width);
      context.stroke();

      requestId = requestAnimationFrame(animate);
    };

    animate();

    //cancelAnimationFrame on unmount
    return () => {
      cancelAnimationFrame(requestId);
    };
  }, []);

  return (
    <div className={styles.mainContainer}>
      <canvas ref={canvasRef} className={styles.canvasContainer} />
      <button onClick={handleClick}>{playAudio ? "STOP" : "LISTEN"}</button>
    </div>
  );
}
