import styles from "../styles/answerFeedback.module.css";

export default function AnswerFeedback(props) {
  let styling;
  if (props.feedback === "correct") {
    styling = styles.correct;
  } else if (props.feedback === "incorrect") {
    styling = styles.incorrect;
  }

  return (
    <div className={styles.mainContainer}>
      <h1 className={styling}>{props.feedback}</h1>
    </div>
  );
}
