import { useEffect, useState } from "react";
import styles from "../styles/question.module.css";
import AudioVisualizer from "./AudioVisualizer";

export default function Question(props) {
  let animate = styles.slideIn;

  const [questionId, setQuestionId] = useState(1);

  async function updateQuestionId() {
    let promise = new Promise((resolve, reject) => {
      setTimeout(() => {
        return resolve("done!");
      }, 500);
    });
    await promise;
    setQuestionId(props.questionId);
  }

  useEffect(() => {
    updateQuestionId();
  });

  if (props.animate === "slideIn") {
    animate = styles.slideIn;
  } else if (props.animate === "slideOut") {
    animate = styles.slideOut;
  }

  return (
    <div className={styles.mainContainer}>
      <div className={animate}>
        <h2>ROAR {questionId}</h2>
      </div>
      <AudioVisualizer audio={props.roar} />
    </div>
  );
}
